module.exports = "/*!\n\n =========================================================\n * Paper Kit - v1.2.2\n =========================================================\n \n * Product Page: http://www.creative-tim.com/product/paper-kit\n * Copyright 2016 Creative Tim (http://www.creative-tim.com)\n * Licensed under MIT (https://github.com/timcreative/paper-kit/blob/master/LICENSE.md)\n \n =========================================================\n \n * The above copyright notice and this permission notice shall be included in all copies or substantial portions of the Software.\n */\n\n\nvar searchVisible = 0;\nvar transparent = true;\n\nvar transparentDemo = true;\nvar fixedTop = false;\n\nvar navbar_initialized = false;\n\n$(document).ready(function() {\n    window_width = $(window).width();\n\n    // Init navigation toggle for small screens   \n    if (window_width < 768) {\n        gsdk.initRightMenu();\n    }\n\n    // Activate Morpghing Buttons \n    $('[data-toggle=\"morphing\"]').each(function() {\n        $(this).morphingButton();\n    });\n\n    //  Activate the tooltips   \n    $('[rel=\"tooltip\"]').tooltip();\n\n    //      Activate the switches with icons \n    if ($('.switch').length != 0) {\n        $('.switch')['bootstrapSwitch']();\n    }\n    //      Activate regular switches\n    if ($(\"[data-toggle='switch']\").length != 0) {\n        $(\"[data-toggle='switch']\").wrap('<div class=\"switch\" />').parent().bootstrapSwitch();\n    }\n\n    //    Activate bootstrap-select\n    if ($(\".selectpicker\").length != 0) {\n        $(\".selectpicker\").selectpicker();\n    }\n\n    if ($(\".tagsinput\").length != 0) {\n        $(\".tagsinput\").tagsInput();\n    }\n\n    if ($('.datepicker').length != 0) {\n        $('.datepicker').datepicker({\n            weekStart: 1,\n            color: '{color}'\n        });\n    }\n\n\n    $('.btn-tooltip').tooltip();\n    $('.label-tooltip').tooltip();\n\n    // Carousel\n    $('.carousel').carousel({\n        interval: 4000\n    });\n\n    $('.form-control').on(\"focus\", function() {\n        $(this).parent('.input-group').addClass(\"input-group-focus\");\n    }).on(\"blur\", function() {\n        $(this).parent(\".input-group\").removeClass(\"input-group-focus\");\n    });\n\n    demo.initPickColor();\n\n    // Make the images from the card fill the hole space\n    gsdk.fitBackgroundForCards();\n\n    // Init icon search action for the navbar\n    gsdk.initNavbarSearch();\n\n    // Init popovers \n    gsdk.initPopovers();\n\n    // Init Collapse Areas\n    gsdk.initCollapseArea();\n\n    // Init Sliders\n    gsdk.initSliders();\n\n    //  Init video card actions\n    gsdk.initVideoCards();\n\n});\n\n// activate collapse right menu when the windows is resized \n$(window).resize(function() {\n    if ($(window).width() < 768) {\n        gsdk.initRightMenu();\n    }\n});\n\ngsdk = {\n    misc: {\n        navbar_menu_visible: 0\n    },\n    initRightMenu: function() {\n        if (!navbar_initialized) {\n            $navbar = $('nav').find('.navbar-collapse').first().clone(true);\n            $navbar.css('min-height', window.screen.height);\n\n            ul_content = '';\n\n            $navbar.children('ul').each(function() {\n                content_buff = $(this).html();\n                ul_content = ul_content + content_buff;\n            });\n\n            ul_content = '<ul class=\"nav navbar-nav\">' + ul_content + '</ul>';\n            $navbar.html(ul_content);\n\n            $('body').append($navbar);\n\n            background_image = $navbar.data('nav-image');\n            if (background_image != undefined) {\n                $navbar.css('background', \"url('\" + background_image + \"')\")\n                    .removeAttr('data-nav-image')\n                    .css('background-size', \"cover\")\n                    .addClass('has-image');\n            }\n\n\n            $toggle = $('.navbar-toggle');\n\n            $navbar.find('a').removeClass('btn btn-round btn-default');\n            $navbar.find('button').removeClass('btn-round btn-fill btn-info btn-primary btn-success btn-danger btn-warning btn-neutral');\n            $navbar.find('button').addClass('btn-simple btn-block');\n\n            $toggle.click(function() {\n                if (gsdk.misc.navbar_menu_visible == 1) {\n                    $('html').removeClass('nav-open');\n                    gsdk.misc.navbar_menu_visible = 0;\n                    $('#bodyClick').remove();\n                    setTimeout(function() {\n                        $toggle.removeClass('toggled');\n                    }, 400);\n\n                } else {\n                    setTimeout(function() {\n                        $toggle.addClass('toggled');\n                    }, 430);\n\n                    div = '<div id=\"bodyClick\"></div>';\n                    $(div).appendTo(\"body\").click(function() {\n                        $('html').removeClass('nav-open');\n                        gsdk.misc.navbar_menu_visible = 0;\n                        $('#bodyClick').remove();\n                        setTimeout(function() {\n                            $toggle.removeClass('toggled');\n                        }, 400);\n                    });\n\n                    $('html').addClass('nav-open');\n                    gsdk.misc.navbar_menu_visible = 1;\n\n                }\n            });\n            navbar_initialized = true;\n        }\n\n    },\n\n    checkScrollForTransparentNavbar: debounce(function() {\n        if ($(document).scrollTop() > 260) {\n            if (transparent) {\n                transparent = false;\n                $('nav[role=\"navigation\"]').removeClass('navbar-transparent');\n            }\n        } else {\n            if (!transparent) {\n                transparent = true;\n                $('nav[role=\"navigation\"]').addClass('navbar-transparent');\n            }\n        }\n    }, 17),\n\n    fitBackgroundForCards: function() {\n        $('.card').each(function() {\n            if (!$(this).hasClass('card-product') && !$(this).hasClass('card-user')) {\n                image = $(this).find('.image img');\n\n                image.hide();\n                image_src = image.attr('src');\n\n                $(this).find('.image').css({\n                    \"background-image\": \"url('\" + image_src + \"')\",\n                    \"background-position\": \"center center\",\n                    \"background-size\": \"cover\"\n                });\n            }\n        });\n    },\n    initPopovers: function() {\n        if ($('[data-toggle=\"popover\"]').length != 0) {\n            $('body').append('<div class=\"popover-filter\"></div>');\n\n            //    Activate Popovers\n            $('[data-toggle=\"popover\"]').popover().on('show.bs.popover', function() {\n                $('.popover-filter').click(function() {\n                    $(this).removeClass('in');\n                    $('[data-toggle=\"popover\"]').popover('hide');\n                });\n                $('.popover-filter').addClass('in');\n            }).on('hide.bs.popover', function() {\n                $('.popover-filter').removeClass('in');\n            });\n\n        }\n    },\n    initCollapseArea: function() {\n        $('[data-toggle=\"gsdk-collapse\"]').each(function() {\n            var thisdiv = $(this).attr(\"data-target\");\n            $(thisdiv).addClass(\"gsdk-collapse\");\n        });\n\n        $('[data-toggle=\"gsdk-collapse\"]').hover(function() {\n                var thisdiv = $(this).attr(\"data-target\");\n                if (!$(this).hasClass('state-open')) {\n                    $(this).addClass('state-hover');\n                    $(thisdiv).css({\n                        'height': '30px'\n                    });\n                }\n\n            },\n            function() {\n                var thisdiv = $(this).attr(\"data-target\");\n                $(this).removeClass('state-hover');\n\n                if (!$(this).hasClass('state-open')) {\n                    $(thisdiv).css({\n                        'height': '0px'\n                    });\n                }\n            }).click(function(event) {\n            event.preventDefault();\n\n            var thisdiv = $(this).attr(\"data-target\");\n            var height = $(thisdiv).children('.panel-body').height();\n\n            if ($(this).hasClass('state-open')) {\n                $(thisdiv).css({\n                    'height': '0px',\n                });\n                $(this).removeClass('state-open');\n            } else {\n                $(thisdiv).css({\n                    'height': height + 30,\n                });\n                $(this).addClass('state-open');\n            }\n        });\n    },\n    initSliders: function() {\n        // Sliders for demo purpose in refine cards section\n        if ($('#slider-range').length != 0) {\n            $(\"#slider-range\").slider({\n                range: true,\n                min: 0,\n                max: 500,\n                values: [75, 300],\n            });\n        }\n        if ($('#refine-price-range').length != 0) {\n            $(\"#refine-price-range\").slider({\n                range: true,\n                min: 0,\n                max: 999,\n                values: [100, 850],\n                slide: function(event, ui) {\n                    min_price = ui.values[0];\n                    max_price = ui.values[1];\n                    $(this).siblings('.price-left').html('&euro; ' + min_price);\n                    $(this).siblings('.price-right').html('&euro; ' + max_price)\n                }\n            });\n        }\n        if ($('#slider-default').length != 0 || $('#slider-default2').length != 0) {\n            $(\"#slider-default, #slider-default2\").slider({\n                value: 70,\n                orientation: \"horizontal\",\n                range: \"min\",\n                animate: true\n            });\n        }\n    },\n    initVideoCards: function() {\n        $('[data-toggle=\"video\"]').click(function() {\n            id_video = $(this).data('video');\n            video = $('#' + id_video).get(0);\n\n            card_parent = $(this).closest('.card');\n\n            if (video.paused) {\n                video.play();\n                $(this).html('<i class=\"fa fa-pause\"></i> Pause');\n                card_parent.addClass('state-play');\n            } else {\n                video.pause();\n                $(this).html('<i class=\"fa fa-play\"></i> Play');\n                card_parent.removeClass('state-play');\n            }\n        });\n    },\n    initNavbarSearch: function() {\n        $('[data-toggle=\"search\"]').click(function() {\n            if (searchVisible == 0) {\n                searchVisible = 1;\n                $(this).parent().addClass('active');\n                $('.navbar-search-form').fadeIn(function() {\n                    $('.navbar-search-form input').focus();\n                });\n            } else {\n                searchVisible = 0;\n                $(this).parent().removeClass('active');\n                $(this).blur();\n                $('.navbar-search-form').fadeOut(function() {\n                    $('.navbar-search-form input').blur();\n                });\n            }\n        });\n    }\n}\n\ndemo = {\n    initPickColor: function() {\n        $('.pick-class-label').click(function() {\n            var new_class = $(this).attr('new-class');\n            var old_class = $('#display-buttons').attr('data-class');\n            var display_div = $('#display-buttons');\n            if (display_div.length) {\n                var display_buttons = display_div.find('.btn');\n                display_buttons.removeClass(old_class);\n                display_buttons.addClass(new_class);\n                display_div.attr('data-class', new_class);\n            }\n        });\n    }\n}\n\nexamples = {\n    initContactUsMap: function() {\n        var myLatlng = new google.maps.LatLng(44.433530, 26.093928);\n        var mapOptions = {\n            zoom: 14,\n            center: myLatlng,\n            scrollwheel: false, //we disable de scroll over the map, it is a really annoing when you scroll through page\n        }\n        var map = new google.maps.Map(document.getElementById(\"contactUsMap\"), mapOptions);\n\n        var marker = new google.maps.Marker({\n            position: myLatlng,\n            title: \"Hello World!\"\n        });\n\n        // To add the marker to the map, call setMap();\n        marker.setMap(map);\n    }\n}\n\n// Returns a function, that, as long as it continues to be invoked, will not\n// be triggered. The function will be called after it stops being called for\n// N milliseconds. If `immediate` is passed, trigger the function on the\n// leading edge, instead of the trailing.\n\nfunction debounce(func, wait, immediate) {\n    var timeout;\n    return function() {\n        var context = this,\n            args = arguments;\n        clearTimeout(timeout);\n        timeout = setTimeout(function() {\n            timeout = null;\n            if (!immediate) func.apply(context, args);\n        }, wait);\n        if (immediate && !timeout) func.apply(context, args);\n    };\n};"


